.right {
  display: flex;
  flex-direction: column;
  flex: 2;
  border-bottom-right-radius: 10px;
  border-top-right-radius: 10px;
}

.right .topMenuRight {
  background-color: #749f82;
  display: flex;
  align-items: center;
  height: 7%;
  padding: 11px 10px 12px 10px;
  border-top-right-radius: 10px;
}

.right .contactNameRight {
  flex: 1;
  margin-top: 19px;
}
.right .rightMenuIcons {
  margin-left: 10px;
  cursor: pointer;
  padding: 2px;
}

.right .rightMenuIcons:hover {
  background-color: rgb(84, 136, 84);
  border-radius: 5px;
}

@media only screen and (max-width: 501px) {
  .right {
    width: 280px;
  }
}
